* {
  margin: 0px;
  padding: 0px;
  font-family: 'Ubuntu', sans-serif;
}

:root {
  --color1:rgb(22, 18, 60);
  --color: #ffffff
}

#root {
  background: url("./d.jpg");
  background-size:cover;
  background-repeat: no-repeat;
}

.App {
  /* background-color: rgba(22, 18, 60, .5); */
  background-color: rgba(22, 18, 60, .5);
}

.chat-room {
  margin: auto;
  max-width: 800px;
  display: flex;
  flex-direction: column;
}

header {
  background-color: rgba(22, 18, 60, .8);
  border-radius: 1rem 1rem 0rem 0rem;
  display: flex;
  justify-content: space-between;
}

header h1 {
  color: white;
  margin: 1rem;
}

#signout-btn {
  height: 100%;
  padding: .5rem;
  margin: 1rem;
  justify-self: center;
  align-self: center;
}

section {
  overflow-y:auto;
  background-color: rgba(43, 43, 44, 0.5);
  height: 80vh;
}

.msg-container {
  display: flex;
  align-items: center;
}

.sent {
  flex-direction: row-reverse;
}

.received {
  flex-direction: row;
}

.msg {
  border-radius: 1rem;
  padding: 1rem;
  max-width: 30rem;
  margin: .5rem;
  background-color: rgba(0, 0, 0, 0.4);
}

.display-name {
  font-weight: bold;
  color: rgb(22, 18, 60);
}

.sent .display-name {
  color: rgba(226, 132, 195, 0.6);
}

.received .display-name {
  color: rgb(152, 144, 219);
}

.msg-text {
  color: white;
}

.msg-container img {
  height: 3rem;
  width: 3rem;
  border-radius: 50%;
  margin: .5rem;
}

#input-field {
  display: flex;
  background-color: rgba(43, 43, 44, 0.5);
  border-radius: 0rem 0rem 1rem 1rem;
}

#input-field input {
  border-radius: 1rem;
  width: 100%;
  height: 3rem;
  font-size: 2rem;
}

#input-field button {
  width: 3rem;
  height: 3rem;
  background-color: rgba(92, 14, 66, .6);
  border-radius: 50%;
}

#input-field button:hover {
  background-color: rgb(22, 18, 60);
}

#input-field button:active {
  background-color: rgb(3, 2, 7);
}
